import { useEffect, useState } from "react"
import { csv2json } from "json-2-csv"
import Chart from "./Chart"
import "./App.css"

function App() {
  const [data, setdata] = useState()
  const [cities, setCities] = useState()
  useEffect(() => {
    const fetchData = async () => {
      const water = await fetch(
        "https://www.data.gouv.fr/fr/datasets/r/2963ccb5-344d-4978-bdd3-08aaf9efe514"
      ).then((res) => res.text())
      setdata(
        csv2json(water, {
          delimiter: { field: ";" },
        })
      )
    }
    const fetchCities = async () => {
      const data = await fetch(
        "https://www.data.gouv.fr/fr/datasets/r/dd9cf705-a759-46c6-afd6-bc85cf25f363"
      ).then((res) => res.text())
      setCities(
        csv2json(data, {
          delimiter: { field: ";" },
        })
      )
    }
    fetchData()
    fetchCities()
  }, [])

  const french = navigator.language === "fr"

  return (
    <>
      <div className="App">
        <header>
          <h1>
            {french
              ? "Surveillance du Sars-CoV-2 dans les eaux usées"
              : "Sars-CoV-2 monitoring in waste water"}
          </h1>
          <p>
            {french
              ? "Un graphique en temps réel de la surveillance de la concentration de Sars-CoV-2 dans les eaux usées. C'est un indicateur de l'intensité des vagues de circulation virale pour le suivi de pandémie de Covid-19 en France."
              : "A real-time graph of wastewater surveillance of SARS-CoV-2 concentration. This is an indicator of the intensity of viral circulation waves for monitoring the COVID-19 pandemic in France."}
          </p>
        </header>
        <Chart data={data} cities={cities} />
        <h2>{french ? "A propos" : "About"}</h2>
        <p>
          {french
            ? "Le dispositif français de surveillance des eaux usées"
            : "The french monitoring system of waste water "}
          <abbr
            title={
              french
                ? "nom du dispositif français de surveillance des eaux usées"
                : "name of the french monitoring system of waste water"
            }
          >
            SUM’Eau
          </abbr>
          {french
            ? ` a été initié sous l’impulsion de la direction générale de la santé et de
        la direction de l’eau et de la biodiversité avec l’appui technique et
        scientifique des agences sanitaires. La surveillance des eaux usées
        poursuit deux objectifs, qui sont de détecter précocement la présence du
        génome du SARS-CoV-2 en population générale et de suivre les tendances
        de la circulation virale au sein de la population.`
            : ` was initiated at the instigation of the Directorate General of Health and the Directorate of Water and Biodiversity, with the technical and scientific support of health agencies. Wastewater surveillance pursues two objectives: early detection of the presence of the SARS-CoV-2 genome in the general population and monitoring trends in viral circulation within the population.`}
        </p>
        <p>
          {french
            ? "✔️ Ces données sont actualisées en temps réel à partir de "
            : "✔️ This data are realtime updated from "}
          <a
            title={
              french
                ? "datagouv, la plateforme de diffusion de données publiques de l'État français."
                : "the French government's public data dissemination platform"
            }
            href="https://www.data.gouv.fr/fr/datasets/surveillance-du-sars-cov-2-dans-les-eaux-usees-sumeau/"
          >
            Datagouv
          </a>
          .
          {french
            ? " Les données de Sum'Eau sont livrées à Datagouv avec une à deux semaines de décalage, donc gardez à l'esprit que c'est un indicateur de la circulation virale de la semaine dernière."
            : "Sum'Eau data is delivered to Data.gouv with a one-to-two week delay, so keep in mind that this is an indicator of last week's viral circulation."}
        </p>
      </div>
      <footer>
        <div>
          {french ? "Fait par un " : "Made by a "}
          <a
            title="twitter account of protocol droid"
            href="https://twitter.com/z8po"
          >
            {french ? "droïde protocolaire" : "protocol droid"}
          </a>{" "}
          {french ? "car " : "because "}
          <i>
            {french
              ? "❝Personne n'aurait pu prédire que [...] Et je ne laisserai personne dire que nous avons manqué à [...]❞"
              : "❝No one could have forseen this [...] I will not let anyone say that we failed to [...]❞"}
          </i>
          {french
            ? " voulait en fait dire 200k décès évitables."
            : " actually meant to say 200k preventable death"}
        </div>
      </footer>
    </>
  )
}

export default App
