import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
  Label,
  Brush,
} from "recharts"

import { useState } from "react"

import moment from "moment"
import chroma from "chroma-js"

export default function Chart({ data = [], cities = [] }) {
  const french = navigator.language === "fr"
  const easter = document?.location?.hash !== ""
  const xAxis = "semaine"

  const waterValues = data?.map((week) =>
    Object.entries(week)
      // remove undefined values for a city this week
      .filter(([name, value]) => value !== "NA")
      // format the city name according to cities dataset
      .map(([name, value]) => {
        if (name !== "National" && name !== xAxis) {
          name = cities?.find(({ nom }) => name === nom)?.commune.toLowerCase()
        }
        return [name, value]
      })
      // re-transform as object
      .reduce(
        (accumulator, [name, value]) => ({
          ...accumulator,
          [name]:
            name === xAxis
              ? //
                moment(value, "YYYY-W").add(7, "d").format("YYYY-MM-DD")
              : Number(value.split(",")[0]),
        }),
        {}
      )
  )
  // set national in top position of the object

  const citiesName = [
    "National",
    ...cities.map(({ commune }) => commune.toLowerCase()),
  ]

  const colors = chroma
    .cubehelix()
    .start(0)
    .rotations(0.8)
    .lightness(0.5)
    .gamma(0.8)
    .hue(4)
    .scale()
    .colors(citiesName.length)

  const [showCity, setShowCity] = useState([
    "National",
    "paris",
    "marseille",
    "toulouse",
  ])

  const filterWaterValue = (weeks, cityNames) =>
    weeks.map((week) =>
      Object.entries(week)
        .filter(([name, value]) => cityNames.includes(name) || name === xAxis)
        .reduce(
          (accumulator, [name, value]) => ({ ...accumulator, [name]: value }),
          {}
        )
    )

  const current = filterWaterValue(waterValues, showCity)

  return (
    <>
      <ResponsiveContainer
        width="90%"
        height={400} // will se max-height
        style={{ overflow: "visible", display: "block" }}
      >
        <LineChart
          data={current}
          margin={{
            right: 30,
            left: 20,
          }}
        >
          {citiesName.map((line, index) => (
            <Line
              strokeWidth={line === "National" ? 5 : 2}
              connectNulls
              type="monotone"
              dot={false}
              dataKey={line}
              key={line}
              stroke={showCity.includes(line) ? colors[index] : "#cccccc"}
              name={line}
              id={line}
            ></Line>
          ))}
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis tickMargin={5} dataKey={xAxis} />
          <YAxis
            label={{
              value: french
                ? "Concentration virale cg/L"
                : "Viral concentration",
              angle: -90,
              position: "left",
              textAnchor: "middle",
            }}
          ></YAxis>
          <Brush
            travellerWidth={15}
            dataKey={(a) => {
              return a[xAxis]
            }}
          />
          <Legend
            width={100}
            align="center"
            wrapperStyle={{
              width: "100%",
            }}
            onClick={(e) => {
              if (showCity.includes(e.value)) {
                setShowCity(showCity.filter((name) => name !== e.value))
              } else {
                setShowCity([...showCity, e.value])
              }
            }}
          />
          <Tooltip
            itemSorter={(e) => {
              return -e.value
            }}
            contentStyle={{
              background: "rgb(33, 37, 43)",
              textAlign: "left",
            }}
          />
          {easter && (
            <ReferenceLine
              x="2022-08-29"
              stroke="transparent"
              label={
                <Label
                  offset={50}
                  value={french ? "❝C'est un rhume❞" : "❝It's mild❞"}
                  position="insideBottom"
                />
              }
            />
          )}
          {easter && (
            <ReferenceLine
              x="2022-12-12"
              stroke="transparent"
              label={
                <Label
                  offset={15}
                  value={
                    french
                      ? "❝Personne n'aurait pu prévoir ça❞"
                      : "❝No one could foreseen this❞"
                  }
                  position="insideTop"
                />
              }
            />
          )}
          {easter && (
            <ReferenceLine
              x="2023-01-30"
              stroke="transparent"
              label={
                <Label
                  offset={50}
                  value={french ? "❝C'est saisonnier❞" : "❝It's seasonal❞"}
                  position="insideBottom"
                />
              }
            />
          )}
          {easter && (
            <ReferenceLine
              x="2023-07-10"
              stroke="transparent"
              label={
                <Label
                  offset={50}
                  value={french ? "❝Ouf C'est terminé❞" : "❝Oof It's over❞"}
                  position="insideBottom"
                />
              }
            />
          )}
          {easter && (
            <ReferenceLine
              x="2023-12-18"
              stroke="transparent"
              label={
                <Label
                  offset={15}
                  value={
                    french
                      ? "❝Personne n'aurait pu prédire ça❞"
                      : "❝No one could predict this❞"
                  }
                  position="insideTop"
                />
              }
            />
          )}
          {easter && (
            <ReferenceLine
              x="2024-04-08"
              stroke="transparent"
              label={
                <Label
                  offset={50}
                  value={french ? "❝Immunité de masse❞" : "❝Herd immunity❞"}
                  position="insideBottom"
                />
              }
            />
          )}
        </LineChart>
      </ResponsiveContainer>
      <p className="info">
        {french
          ? "🛈 Cliquez sur la légende pour sélectioner le détail d'une ville. Ou directement afficher:"
          : "🛈 Click on the legend to select a city. Or directly display:"}
        <button
          className="reset"
          data={{}}
          onClick={(e) => {
            showCity.length === citiesName.length
              ? setShowCity(["National"])
              : setShowCity(citiesName)
          }}
        >
          {showCity.length === citiesName.length
            ? french
              ? "La moyenne nationale"
              : "National average"
            : french
            ? "Toutes les villes"
            : "All cities"}
        </button>{" "}
        <br />{" "}
        {french
          ? "🛈 Utilisez la goutière sous le graphique pour cibler une période dans les données."
          : "🛈 Use the gutter below the graph to target a specific timeseries"}
      </p>
    </>
  )
}
